                </v-img>

                <v-card-subtitle style="text-transform: capitalize">
                  <div class="d-flex flex-row mb-2">
                    <!-- <div
                        v-if="list.name.length > 26"
                        class="d-flex flex-column"
                      >
                        <v-tooltip
                          bottom
