                        label
                        outlined
                      >
                        (200 Review)
                      </v-chip>
                    </div>
                  </div>

                  <!-- <div class="grey--text ms-4">
                  4.5 (413)
                </div> -->
                </v-card-subtitle>

                <v-card-actions>
                  <v-btn
                    color="orange"
                    text
                    @click="toPush(`${item.slug}/home`)"
                  >
                    {{ $t("dahsboard.sxp") }}
                  </v-btn>
