                      </v-chip>
                    </v-col>
                    <v-col
                      class="col__"
                      cols="12"
                    >
                      <v-chip
                        label
                        class="font-spartan mr-1 text-uppercase"
                        color="btn_primary"
                        text-color="white"
                        small
                        @click.stop=""
                      >
                        {{ item.category.name }}
                      </v-chip>
                    </v-col>
                  </v-row>
                </v-img>
                <v-img
                  v-else
                  class="white--text align-end"
                  height="200"
                  src="http://localhost:8081/img/et.7701c625.png"
                  style="cursor:pointer"
                  gradient="to top right, rgba(0, 0, 0, 0.05), rgba(0, 0, 0, 0.05)"
                >
                  <v-row
