  </v-container>
</template>

<script>
  export default {
    data: () => ({
      is_load: true,
    }),
    computed: {
      users () {
        const Me = localStorage.getItem("ME")
        const users = JSON.parse(Me)

        return users
      },
      studio () {
        return this.$store.state.studioPopuler.data
      },
      classes () {
        return this.$store.state.classesPopuler.data
      },
    },
    mounted () {
      this.getDataMostPopuler()
      this.getDataPopulerClasses()
    },
    methods: {
      getDataMostPopuler () {
        this.$store
          .dispatch("studioPopuler/getDataMostPopuler", {
            entities: "img, followers, likes",
          })
          .then(res => {
            if (res.data.meta.status) {
              this.is_load = false
            }
          })
      },
      getDataPopulerClasses () {
        this.$store.dispatch("classesPopuler/getDataPopulerClasses", {
          entities: "category, img,studio",
        })
      },
      toPush (link) {
        this.$router.push(link)
      },
    },
  }
</script>

<style lang="sass">
a
  text-decoration: underline
.theme--dark
  .divider--opacity
    opacity: .2
.mt-g
  margin-top: -200px
.col__
  margin-top: -20px
.card__titleImg
  margin-top: 100px
  // width: 90px !important
  .v-chip__content
    margin-left: 8px !important
    font-size: 20px
    font-weight: 900 !important
.font__price
  font-weight: 900
  font-size: 1.6rem
  color: orange !important
.hover--text
  &:hover
    color: #4CAF50 !important
</style>
